@font-face {
  font-family: BebasNeue;
  src: url('../../../../assets/fonts/BebasNeue Regular.otf');
}

@font-face {
  font-family: BebasNeueBold;
  font-weight: bold;
  src: url('../../../../assets/fonts/BebasNeue Bold.otf');
}

.Results {
  width: 100%;
  margin-bottom: 50px;
}

.ResultsContainer {
  width: 50%;
  margin: 20px 25%; 
}

.RaceBanner {
  width: 50%;
  margin-left: 25%; 
  -o-object-fit: contain; 
     object-fit: contain;
}

.RaceResultsShareButton {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin-top: 10px;
  border: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.RaceResultsShareButton:hover {
  cursor: pointer;
}

.RaceResultsShareButton img {
  height: 30px;
}

.WaveSelect {
  width: 100%;
  background-color: #1e4d66;
  padding: 20px 10px;
  margin: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.WaveSelect select {
  height: 30px;
  width: 70%;
  margin-left: 15%;
  padding: 5px;
  border: none;
  background-color: white;
  color: rgba(0,0,0,0.8);
  font-size: 15px;
  font-weight: bold;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.WaveSelect select:focus {
  outline: none;
}

.WaveSelect select > option {
  color: rgba(0,0,0,0.8);
  font-size: 15px;
  font-weight: bold;
}

.TableLabel {
  width: 100%;
  margin: 20px 0 10px 0;
  padding: 5px;
  background-color: #1e4d66;
  color: white;
  font-size: 1.2em;
  font-weight: 500;
  text-align: left;
  font-family: BebasNeueBold;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

@media (max-width: 499px) {
  .ResultsContainer {
    width: 90%;
    margin: 20px 5%;  
  }

  .RaceBanner {
    width: 100%;
    margin: 0;
  }

  .TableLabel {
    margin-bottom: 0;
  }

  .WaveSelect {
    width: 100%;
    background-color: white;
    padding: 0;
  }

  .WaveSelect select {
    width: 100%;
    padding: 5px;
    margin: 0;
    border: 1px solid #ddd;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    background-color: white;
  }
}


