.BottomNav {
  width: 100%;
  max-width: 100%;
  background-color: #1e4d66; /*#afafaf;*/
  position: fixed;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

@media (min-width: 500px) {
  .BottomNav{
    display: none;
  }
}


