@font-face {
  font-family: BebasNeue;
  src: url('../../../assets/fonts/BebasNeue Regular.otf');
}

@font-face {
  font-family: BebasNeueBold;
  font-weight: bold;
  src: url('../../../assets/fonts/BebasNeue Bold.otf');
}

.UpcomingRaces {
  margin: 30px 10%;
  background-color: white;
}

.RacesContainer {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.MobileSearchGroup {
  display: none;
}

@media (max-width: 499px) {
  .UpcomingRaces {
    padding: 10px 0;
    margin-bottom: 70px;
  }

  .MobileSearchGroup {
    width: 100%;
    padding: 5px 10px;
    background-color: #1e4d66;
    border: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: fixed;
    bottom: 0;
    left: 0;
  }
}


