.Carousel {
  margin-top: 56px;
}

.SlideIn {
  -webkit-transform: translateX(100%);
      -ms-transform: translateX(100%);
          transform: translateX(100%);
}

.SlideOut {
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
}

.Active {
  display: block;
}

.Hidden {
  display: none;
}

.VisuallyHidden {
  opacity: 0;
}

@-webkit-keyframes fading {
  0% {
    opacity:0.2
  }
  50% {
    opacity:1
  }
  100% {
    opacity:0.2
  }
}

@keyframes fading {
  0% {
    opacity:0.2
  }
  50% {
    opacity:1
  }
  100% {
    opacity:0.2
  }
}

.Carousel img {
  width: 100%;
  margin: 0;
  padding: 0;
  -webkit-animation:fading 15s 1;
          animation:fading 15s 1;
}

@media (max-width: 499px) {
  .Carousel {
    margin-top: 0px;
  }
}


